<template>
<section>
  <mt-popup position="top" :modal="false" v-model="popupTopVisible" style="width:100%;background:none;">
    <div class="search_modal">
      <div class="search_wrapper">
        <input type="text" class="input_text">
        <div class="search_btn">搜索</div>
        <div class="search_btn bg_danger" @click="popupTopVisible=false">取消</div>
      </div>
    </div>
  </mt-popup>
  <mt-popup position="left" :modal="true" v-model="popupLeftVisible" style="width:70%;background:#fff;max-width:300px;height:100%;">
    <div class="aside_modal">
      <div class="aside_head">
      <div class="menu_btn menu_btn_aside" @click="popupLeftVisible=!popupLeftVisible"><i class="iconfont icon-fanhui"></i></div>
        <div class="aside_avatar"><img src="http://img1.imgtn.bdimg.com/it/u=1062502429,3132037224&fm=200&gp=0.jpg"></div>
        <div class="aside_title">蔡胜龙的博客</div>
      </div>
    </div>
  </mt-popup>
  <div class="main">
    <!-- 菜单按钮 -->
    <div class="menu_btn menu_btn_left" @click="popupLeftVisible=!popupLeftVisible"><i class="iconfont icon-liebiao"></i></div>
    <div class="menu_btn menu_btn_right" @click="popupTopVisible=!popupTopVisible"><i class="iconfont icon-sousuosearch79"></i></div>
    <!-- 主体内容 -->
    <div class="content_panel">
      <div class="author_pan blog_item">
        <div class="avatar_temp">
          <a class="avatar_wrapper" href=""><img src="http://m.qpic.cn/psb?/V14UvRvG0Jhof1/Z2SHgfF7GS4vvQJJleWGcZSk9xx41i6XaCWC*K666X0!/b/dGoBAAAAAAAA&bo=QAaEA0AGhAMROQ4!&rf=viewer_4" alt=""></a>
        </div>
        <div class="func_temp">
          <div class="csl_blog">
            <strong>蔡胜龙的博客</strong>
          </div>
          <div class="csl_func">
            <span class="icon_btn"><i class="iconfont icon-tiyu-kualan"></i></span>
            <span class="icon_btn"><i class="iconfont icon-78lvxing"></i></span>
          </div>
        </div>
      </div>
      <v-item :class="{'home_top_item':true}"></v-item>
      <v-item></v-item>
      <v-item></v-item>
      <v-item></v-item>
    </div>
  </div>
  <footer>
    <div class="csl_any"><a class="icon_btn" href=""><i class="iconfont icon-GitHub"></i></a></div>
    <div class="csl_right">Copyright © 2015 - 2018 蔡胜龙的博客</div>
    <div class="csl_other"></div>
  </footer>
</section>
</template>

<script>
import item from "@/components/item/item.vue"
export default {
  name: 'App',
  data() {
    return {
      popupTopVisible:false,
      popupLeftVisible:false,
    }
  },
  components: {
    'v-item': item,
  }
}
</script>

<style scope>
  @media screen and (min-width:500px){
    .content_panel .home_top_item.blog_item{width:65%;}
    .content_panel .author_pan{width:33%;margin-bottom:2.2rem;}
  }
  @media screen and (max-width:500px){
    .content_panel .home_top_item.blog_item{width:100%;min-height:8rem;}
    .content_panel .author_pan{
      width:100%;
      padding:10px;
      margin-bottom:0;
    }
  }
  .author_pan{
    background-color:#fff;
    box-sizing:border-box;
  }
  .main{
    width:100%;
    height:auto;
    padding-top:6rem;
  }
  .menu_btn{
    padding:10px 20px;
    position:fixed;
    top:1pc;
    cursor:pointer;
  }
  .menu_btn .iconfont{
    font-size:20px;
    color:#0E52A3;
  }
  .menu_btn_right{
    right:0;
  }
  .content_panel{
    width:100%;
    max-width:900px;
    margin:0 auto;
    display:flex;
    align-items: stretch;
    box-sizing: border-box;
    justify-content: space-around;
    flex-flow: row wrap;
  }

  .avatar_temp{
    display:flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content:space-around;
  }
  .avatar_wrapper{
    display:inline-block;
    width:10rem;
    height:10rem;
    margin:0 auto;
    border-radius:50%;
    overflow:hidden;
  }
  .avatar_wrapper img{
    width:100%;
    height:100%;
  }
  .func_temp{
    padding:0.8rem;
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    font-size:15px;
  }
  .func_temp .csl_blog{
    margin-right:1rem;
  }
  .icon_btn{
    display:inline-block;
    padding:0 0.3rem;
    cursor:pointer;
  }
  .icon_btn .iconfont{
    font-size:25px;
  }
  footer{
    display:flex;
    flex-direction:row;
    justify-content: space-between;
    padding:2rem;
    background:#fff;
    margin-top:2rem;
  }
  footer .csl_right{
    font-size:14px;
    color:#999;
  }
  .search_modal{
    width:100%;
    height:4rem;
    background:rgba(0,0,0,0.6);
    display:flex;
    justify-content: space-around;
  }
  .search_wrapper{
    display:flex;
    width:90%;max-width: 900px;
    padding:1rem;
  }
  .input_text{
    flex-grow: 1;
    padding:0 10px;
    border-radius:5px;
    border:none;
  }
  .search_btn{
    display:inline-block;
    padding:0 1rem;
    height:2rem;
    line-height:2rem;
    font-size:0.7rem;
    background:#409EFF;
    box-sizing:border-box;
    cursor:pointer;
    color:#fff;
    border-radius:2px;
    margin:0 2px;
  }
  .bg_danger{background-color:#F56C6C !important;}
  .aside_modal{
    width:100%;
    background:#fff;
    min-height:100%;
    display:flex;
    flex-direction: column;
  }
  .aside_head{
    display:flex;
    flex-direction: column;
    height:12rem;
    background:#333;
    box-sizing:border-box;
    padding-top:30px;
  }
  .aside_avatar{
    padding:0 1rem;
    flex-grow:1;
    display:flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .aside_avatar img{
    border-radius:50%;
    width:6rem;
    height:6rem;
  }
  .aside_title{
    padding:0.5rem 1rem;
    font-size:0.7rem;
    color:#fff;
  }
  .menu_btn_aside{
    top:4rem;
    right:0.5rem;
  }
  .menu_btn_aside .iconfont{color:#fff;}
</style>



